.header {
    display: grid;
    grid-template-columns: auto auto auto ;

    width: 100%;
    height: 60px;
    background: rgba(25, 25, 25, 0.99);
    align-content: center;
    z-index: 1;

    color: white;
    top:0;
    align-items: center;
    justify-items: center;

  
   
  }

  .header a {
    text-decoration: none;
    color: inherit;
    text-shadow: inherit;  
  }

  .logout {
    font-size: 0.9em;
    cursor: pointer;
  }

  .headerIcon {
    display: inline-block;
    justify-self: center;
  }
  

.headerIcon:hover {
  text-shadow: 0 0 5px #f7f9f6;

}


  
 
  

  


  